//функция для скрытия навигационной панели при клике не на кнопку меню или при скролле окна
let hideHideNav = (e) => {
    if(e.target == document.getElementsByClassName('hide_nav')[0] || 
    //моя другая функция производит нажатие на радио-кнопки что бы они правильно работали и дабы
    //панель не скрывалась при таком нажатии радио-кнопки тоже были добавлены в исключение
    e.target == document.getElementsByClassName('banner_sliderControl_button')[0] ||
    e.target == document.getElementsByClassName('banner_sliderControl_button')[1] ||
    e.target == document.getElementsByClassName('banner_sliderControl_button')[2]) return;
    let hideNav = document.getElementsByClassName('hide_nav_pan')[0];
    hideNav.style.display = 'none';
};
window.addEventListener('click', hideHideNav);
window.addEventListener('scroll', hideHideNav);
//функция показать-спрятать при клике на кнопку меню
let showHideNav = () => {
    let hideNav = document.getElementsByClassName('hide_nav_pan')[0];
    if(hideNav.style.display === 'none') {
        hideNav.style.display = 'block';
    } else if(hideNav.style.display === 'block') {
        hideNav.style.display = 'none';
    };
};

