let pricing_slider_previos = () => {
    let pricing_block1 = document.getElementsByClassName('pricing_table_part')[0];
    let pricing_block2 = document.getElementsByClassName('pricing_table_part')[1];
    let pricing_block3 = document.getElementsByClassName('pricing_table_part')[2];
    if(getComputedStyle(pricing_block2).display === 'block') {
        pricing_block1.style.display = 'block';
        pricing_block1.style.animationName = 'slideout';
        pricing_block2.style.animationName = 'outright';
        setTimeout(()=> {pricing_block2.style.display = 'none';}, 700);
    } else if(getComputedStyle(pricing_block1).display === 'block') {
        pricing_block3.style.display = 'block';
        pricing_block3.style.animationName = 'slideoutSpec';
        pricing_block1.style.animationName = 'outrightSpec';
        setTimeout(()=> {pricing_block1.style.display = 'none';}, 700);
    } else if(getComputedStyle(pricing_block3).display === 'block') {
        pricing_block1.style.display = 'none';
        pricing_block1.style.animationName = 'slidein';
        pricing_block2.style.display = 'block';
        pricing_block2.style.animationName = 'slideout';
        pricing_block3.style.animationName = 'outright';
        setTimeout(()=> {pricing_block3.style.display = 'none';}, 700);
        
    };
};

let pricing_slider_next = () => {
    let pricing_block1 = document.getElementsByClassName('pricing_table_part')[0];
    let pricing_block2 = document.getElementsByClassName('pricing_table_part')[1];
    let pricing_block3 = document.getElementsByClassName('pricing_table_part')[2];
    if(getComputedStyle(pricing_block2).display === 'block') {
        pricing_block1.style.display = 'none';
        pricing_block1.style.animationName = 'slideout';
        pricing_block3.style.display = 'block';
        pricing_block3.style.animationName = 'slidein';
        pricing_block2.style.animationName = 'outleft';
        setTimeout(()=> {pricing_block2.style.display = 'none';}, 700);
    } else if(getComputedStyle(pricing_block1).display === 'block') {
        pricing_block2.style.display = 'block';
        pricing_block2.style.animationName = 'slidein';
        pricing_block3.style.display = 'none';
        pricing_block3.style.animationName = 'slideout';
        pricing_block1.style.animationName = 'outleft';
        setTimeout(()=> {pricing_block1.style.display = 'none';}, 700);
    } else if(getComputedStyle(pricing_block3).display === 'block') {
        pricing_block1.style.display = 'block';
        pricing_block1.style.animationName = 'slideinSpec';
        pricing_block2.style.display = 'none';
        pricing_block2.style.animationName = 'slideout';
        pricing_block3.style.animationName = 'outleftSpec';
        setTimeout(()=> {pricing_block3.style.display = 'none';}, 700);
    };
};
let rebloched = () => { 
    if(window.innerWidth > 535) {
        let pricing_block1 = document.getElementsByClassName('pricing_table_part')[0];
        let pricing_block2 = document.getElementsByClassName('pricing_table_part')[1];
        let pricing_block3 = document.getElementsByClassName('pricing_table_part')[2];
        pricing_block1.style.display = 'block';
        pricing_block2.style.display = 'block';
        pricing_block3.style.display = 'block';
    } else if(window.innerWidth <= 535) {
        let pricing_block1 = document.getElementsByClassName('pricing_table_part')[0];
        let pricing_block2 = document.getElementsByClassName('pricing_table_part')[1];
        let pricing_block3 = document.getElementsByClassName('pricing_table_part')[2];
        if(getComputedStyle(pricing_block1).display === 'block') {
            pricing_block1.style.display = 'block';
            pricing_block2.style.display = 'none';
            pricing_block3.style.display = 'none';
        } else if(getComputedStyle(pricing_block2).display === 'block') {
            pricing_block1.style.display = 'none';
            pricing_block2.style.display = 'block';
            pricing_block3.style.display = 'none';
        } else if(getComputedStyle(pricing_block3).display === 'block') {
            pricing_block1.style.display = 'none';
            pricing_block2.style.display = 'none';
            pricing_block3.style.display = 'block';
        };
    };
};
window.addEventListener('resize', rebloched);

